<template>
  <div class="politica-privacidade-wrapper">
    <h1>Política de Privacidade e Protecção de Dados</h1>
    <div class="topics">
      <div class="topic" v-for="p in politica" :key="p.id">
        <div class="title">{{p.title}}</div>
        <div class="content" v-html="p.content"></div>
      </div>
    </div>

    <!-- <h1>Termos e Condições</h1>
    <div class="topics">
      <div class="topic" v-for="t in termos" :key="t.id">
        <div class="title">{{t.title}}</div>
        <div class="content" v-html="t.content"></div>
      </div>
    </div>-->
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
// import FadeIn from "../animations/fade";

export default {
  data() {
    return {
      // FadeIn,
      politica: [
        {
          id: 1,
          title: "",
          content: "",
        },
      ],
      // termos: [
      //   {
      //     id: 1,
      //     title: "",
      //     content: "",
      //   },
      // ],
    };
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getTermosCondicoes.php")
      .then((resp) => {
        const data = resp.data;

        // var result = data.split("||");
        // var tc = JSON.parse(result[1]);
        // var tcTopics = [];
        // var pp = JSON.parse(result[1]);
        var ppTopics = [];

        // map.call(tc, function (d) {
        //   tcTopics.push({
        //     id: d.id,
        //     title: d.title,
        //     content: d.content,
        //   });
        // });

        map.call(data, function (d) {
          ppTopics.push({
            id: d.id,
            title: d.title,
            content: d.content,
          });
        });

        // vm.$data.termos = tcTopics;
        vm.$data.politica = ppTopics;
      })
      .then(() => {
        var links = document.querySelectorAll(".content > a");

        map.call(links, function (a) {
          a.style.color = "#0babc5 !important";
          a.style.textDecoration = "none !important";
          a.target = "_blank";
        });
      });
  },
  metaInfo() {
    return {
      title:
        "Política de Privacidade - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content:
            "Consulte a Política de Privacidade e os Termos e Condições para estar a par das condições de navegação do nosso site.",
        },
        {
          property: "og:title",
          content:
            "Política de Privacidade - Ana Carolina Pereira - Clínica de Psicologia",
        },
        {
          property: "og:url",
          content: "https://www.anacarolinapereira.pt/politica-privacidade",
        },
        {
          property: "og:description",
          content:
            "Consulte a Política de Privacidade e os Termos e Condições para estar a par das condições de navegação do nosso site.",
        },
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.politica-privacidade-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  h1 {
    margin-top: pxToEm(100);
    text-align: center;
    color: $turquoise;
    font-size: pxToEm(42);
  }

  .topics {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-bottom: pxToEm(50);
    max-width: pxToEm(1400);
    width: 100%;

    .topic {
      margin: pxToEm(25) 0;

      .title {
        font-size: pxToEm(32);
        color: $turquoise;
        margin-bottom: pxToEm(10);
      }

      .content {
        font-size: pxToEm(24);
        text-align: justify;
        text-align-last: left;
        color: #707070;
      }
    }
  }
}
</style>




